import { css } from 'lit';

export const styles = css`:host {
  --item-text-color: var(--ig-tabs-item-text-color, hsla(var(--ig-gray-700), var(--ig-gray-a)));
  --item-background: var(--ig-tabs-item-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --item-hover-background: var(--ig-tabs-item-hover-background, hsla(var(--ig-gray-200), var(--ig-gray-a)));
  --item-hover-color: var(--ig-tabs-item-hover-color, hsla(var(--ig-gray-700), var(--ig-gray-a)));
  --item-active-color: var(--ig-tabs-item-active-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --item-icon-color: var(--ig-tabs-item-icon-color, hsla(var(--ig-gray-700), var(--ig-gray-a)));
  --item-active-icon-color: var(--ig-tabs-item-active-icon-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --item-hover-icon-color: var(--ig-tabs-item-hover-icon-color, hsla(var(--ig-gray-700), var(--ig-gray-a)));
  --item-disabled-icon-color: var(--ig-tabs-item-disabled-icon-color, hsla(var(--ig-gray-500), var(--ig-gray-a)));
  --item-active-background: var(--ig-tabs-item-active-background, hsla(var(--ig-gray-200), var(--ig-gray-a)));
  --item-disabled-color: var(--ig-tabs-item-disabled-color, hsla(var(--ig-gray-500), var(--ig-gray-a)));
  --indicator-color: var(--ig-tabs-indicator-color, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --button-color: var(--ig-tabs-button-color, hsla(var(--ig-gray-700), var(--ig-gray-a)));
  --button-background: var(--ig-tabs-button-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --button-hover-background: var(--ig-tabs-button-hover-background, hsla(var(--ig-gray-100), var(--ig-gray-a)));
  --button-hover-color: var(--ig-tabs-button-hover-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --button-disabled-color: var(--ig-tabs-button-disabled-color, hsla(var(--ig-gray-500), var(--ig-gray-a)));
  --tab-ripple-color: var(--ig-tabs-tab-ripple-color, hsla(var(--ig-gray-100), var(--ig-gray-a)));
  --button-ripple-color: var(--ig-tabs-button-ripple-color, hsla(var(--ig-gray-100), var(--ig-gray-a)));
  --border-color: var(--ig-tabs-border-color, transparent);
  --border-color--hover: var(--ig-tabs-border-color--hover, transparent);
  --border-radius: var(--ig-tabs-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0) * 0.375rem), 0.375rem));
  background: var(--item-background);
  color: var(--item-text-color);
  border-radius: var(--border-radius);
}

:host(:hover),
:host(:focus-within) {
  background: var(--item-hover-background);
  color: var(--item-hover-color);
}

:host([selected]) {
  color: var(--item-active-color);
}
:host([selected]) ::slotted(igc-icon) {
  color: var(--item-active-icon-color);
}

:host([selected]:hover),
:host([selected]:focus-within) {
  color: var(--item-active-color);
}

:host([disabled]) {
  color: var(--item-disabled-color);
}`;
